.main-background

  &__wrap
    width: 100%
    height: 100vh
    position: fixed
    @include pin(-1px,0,0,0)
    background-image: url("../img/bg-light.png")
    background-size: cover
    z-index: -1
    pointer-events: none
    background-position-x: center

.bg-night
  background-image: url("../img/bg-dark.png")